<template>
  <div class="mycenter">
    <div class="info">
      <img :src="zheadimgurl" class="headimg" />
      <div class="infokey">
        <div>车主姓名</div>
        <div>车主手机</div>
        <div>车牌信息</div>
      </div>
      <div class="infoval">
        <div>{{zname}}</div>
        <div>{{zmobile}}</div>
        <div>{{zlicense}}</div>
      </div>
    </div>
    <van-cell class="cell1" title="我的全心伙伴" is-link to="adviser" />
    <van-cell class="cell2" title="我的优惠券" is-link to="coupon" />
    <van-cell class="cell3" title="我的预约" is-link to="historyswiper" />
  </div>
</template>
<script>
export default {
  name: "mycenter",
  data() {
    return {
      zheadimgurl: "",
      zname: "",
      zmobile: "",
      zlicense:""
    };
  },
  created() {
    this.getDriverInfo();
  },
  methods: {
    getDriverInfo() {
      let _this = this;
      this.$axios
        .get(domain + "vehicle/detail", {})
        .then(res => {
          _this.zheadimgurl = res.data.data.headimgurl;
          _this.zname = res.data.data.name;
          _this.zmobile = res.data.data.mobile;
          _this.zlicense=res.data.data.licenseCode
        })
        .catch(function(err) {
          console.log(err);
        });
    }
  }
};
</script>
<style lang="less">
.mycenter {
  font-size: 13px;
  .info {
    margin-top: 90px;
    border-radius: 6px;
    background: rgba(150, 150, 150, 0.3);
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 25px 0 25px 20px;
    margin-bottom: 15px;
    .headimg {
      width: 55px;
    }
    .infokey {
      color: #8a8a8a;
      margin: 0 15px;
    }
    .infoval {
      color: #fff;
    }
  }
  .van-cell {
    border-radius: 6px;
    background: rgba(150, 150, 150, 0.3);
    color: #fff;
    padding: 12px;
    margin-bottom: 15px;
    &.cell1:before {
      content: " ";
      display: block;
      height: 20px;
      width: 20px;
      background: url(../assets/images/huoban.png) no-repeat;
      background-size: 100%;
      margin-right: 15px;
    }
    &.cell2:before {
      content: " ";
      display: block;
      height: 20px;
      width: 20px;
      background: url(../assets/images/youhuiquan.png) no-repeat;
      background-size: 100%;
      margin-right: 15px;
    }
    &.cell3:before {
      content: " ";
      display: block;
      height: 20px;
      width: 20px;
      background: url(../assets/images/yuyue.png) no-repeat;
      background-size: 100%;
      margin-right: 15px;
    }

    .van-cell__right-icon {
      color: #fff;
    }
  }
  .van-cell:not(:last-child)::after {
    border-bottom-width: 0;
  }
}
</style>


