<template>
  <div class="historyswiper">
    <userhead />
    <van-loading type="spinner" v-show="showload" style="text-align:center;margin-bottom:10px;" />
    <div v-show="!showload">
      <swiper :options="swiperOption">
        <swiper-slide v-for="(e,i) in listArr" :key="i">
          <div class="box1">
            <div class="infoleft">
              <div>预约状态:</div>
            </div>
            <div class="inforight">
              <span>{{e.status.text}}</span>
            </div>
          </div>
          <div class="box1">
            <div class="infoleft">
              <div>预约类型:</div>
              <div>在线预约时间:</div>
              <div>行驶里程:</div>
              <div>预约进店时间:</div>
            </div>
            <div class="inforight">
              <div>{{e.type.text}}</div>
              <div>{{e.createTime}}</div>
              <div>{{e.mileage}}公里</div>
              <div>{{e.orderTime}}</div>
            </div>
          </div>
          <div class="box1">
            <div class="infoleft">
              <div>接待门店:</div>
              <div>接待联系人:</div>
            </div>
            <div class="inforight">
              <div>{{e.poiName}}</div>
              <div>{{e.adviserName}}</div>
            </div>
          </div>
        </swiper-slide>
      </swiper>
      <div class="swiper-button-prev swiper-button-white"></div>
      <div class="swiper-button-next swiper-button-white"></div>
      <div class="swiper-pagination swiper-pagination-bullets" slot="pagination"></div>
    </div>
  </div>
</template>
<script>
import userhead from "../modal/userhead";
import "swiper/dist/css/swiper.css";
import { swiper, swiperSlide } from "vue-awesome-swiper";
export default {
  name: "historyswiper",
  components: {
    userhead,
    swiper,
    swiperSlide
  },
  data() {
    return {
      swiperOption: {
        navigation: {
          nextEl: ".swiper-button-next",
          prevEl: ".swiper-button-prev"
        },
        pagination: {
          el: ".swiper-pagination",
          type: "fraction"
        }
      },
      listArr: [],
      showload: true
    };
  },
  created() {
    this.getSwiperList();
  },
  methods: {
    getSwiperList(id) {
      this.detail = true;
      this.$axios
        .get(domain + "appointment/list", {
          params: {
            customerId: 111111
          }
        })
        .then(res => {
          this.showload = !this.showload;

          res.data.data.forEach(e => {
            this.listArr.push({
              createTime: e.createTime,
              mileage: e.mileage,
              orderTime: e.orderTime,
              poiName: e.poiName,
              adviserName: e.adviserName,
              status: {
                text: e.status.text
              },
              type: {
                text: e.type.text
              }
            });
          });
        })
        .catch(function(err) {
          console.log(err);
        });
    }
  }
};
</script>
<style lang="less">
.historyswiper {
  color: #8a8a87;
  font-size: 13px;
  position: relative;
  //   处理滑动时候圆角问题
  .swiper-container {
    border-radius: 6px;
  }
  .swiper-slide {
    border-radius: 6px;
    background: rgba(150, 150, 150, 0.3);
    .box1 {
      display: flex;
      justify-content: flex-start;
      border-bottom: 1px solid #5b5f66;
      padding: 12px 10px 12px 30px;
      &:last-child {
        border-width: 0;
      }
      .infoleft {
        text-align: left;
        width: 100px;
        line-height: 30px;
      }
      .inforight {
        color: #fff;
        line-height: 30px;
        text-align: left;
        > span {
          display: block;
          height: 30px;
          width: 70px;
          border-radius: 5px;
          border: 1px solid #f10100;
          background: #c41d24;
          text-align: center;
        }
      }
    }
  }

  // 页码style
  .swiper-pagination {
    position: unset;
    color: #fff;
    margin-top: 10px;
  }
  .swiper-pagination-bullet-custom {
    width: 20px;
    height: 20px;
    text-align: center;
    line-height: 20px;
    font-size: 12px;
    color: #000;
    opacity: 1;
    background: rgba(0, 0, 0, 0.2);
  }
  .swiper-pagination-bullet-custom.swiper-pagination-bullet-active {
    color: #fff;
    border: 1px solid #f10100;
    background: #c41d24;
    &:focus {
      outline: solid #ff0000 0px;
    }
  }
  .swiper-pagination-bullet {
    border: 1px solid #797c7b;
    color: #797c7b;
    margin: 5px;
  }

  //   方向
  .swiper-button-prev {
    left: -10px;
    margin-top: 0;
    &:focus {
      outline: solid #ff0000 0px;
    }
  }
  .swiper-button-next {
    right: -10px;
    margin-top: 0;
    &:focus {
      outline: solid #ff0000 0px;
    }
  }
}
</style>


