<template>
  <div class="info">
    <div class="infotxt">
      <div>欢迎您</div>
      <div>{{firstName}}</div>
    </div>
    <img src="../assets/images/ueserbind-headimg.png" />
  </div>
</template>
<script>
export default {
  name: "userhead",
  data() {
    return {
      firstName: ""
    };
  },
  created() {
    this.$axios
      .get(domain + "vehicle/detail", {})
      .then(res => {
        this.firstName = res.data.data.name;
        window.name = res.data.data.name;
      })
      .catch(function(err) {
        console.log(err);
      });
  }
};
</script>
<style lang="less" scoped>
.info {
  margin-top: 30px;
  margin-bottom: 30px;
  color: #fff;
  height: 40px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  .infotxt {
    text-align: right;
    > div:first-child {
      font-size: 13px;
    }
    > div:last-child {
      font-size: 16px;
    }
  }
  > img {
    height: 40px;
    margin-left: 10px;
  }
}
</style>


