<template>
  <div class="callhelp">
    <userhead />
    <h2 v-show="this.$route.query.type=='baoxian'">您的专属保险顾问</h2>
    <h2 v-show="this.$route.query.type=='jinji'">您的专属紧急救援顾问</h2>
    <h2 v-show="this.$route.query.type=='shigu'">您的专属事故顾问</h2>

    <div class="infobox">
      <img :src="info.headimg" alt />
      <div class="name">{{info.name}}</div>
      <div class="shop">{{info.place}}</div>
      <div class="year">{{info.year}}</div>
      <div class="twobox">
        <div @click="dotel">
          <van-icon name="phone-o" />
          <span>电话咨询</span>
        </div>
        <div>
          <van-icon name="chat-o" />
          <span>在线咨询</span>
        </div>
      </div>
    </div>

    <van-swipe
      v-show="this.$route.query.type=='baoxian'"
      ref="zswiper"
      :autoplay="3000"
      indicator-color="white"
      :show-indicators="true"
    >
       <van-swipe-item v-for="(v,i) in imgarr" :key="i" :style="{'background-image':'url('+v+')'}">
      </van-swipe-item>
    </van-swipe>

    <van-popup :overlay="false" :round="true" v-model="telalert" closeable>
      <h3>您的顾问电话</h3>
      <a
        class="teldiv"
        :href="'tel:'+info.tel"
        @click="recordtel(1,info.adviserId,advisertype)"
      >{{info.tel}}</a>
      <p>请直接点击拨打</p>
    </van-popup>
  </div>
</template>
<script>
import userhead from "../modal/userhead";
export default {
  name: "callhelp",
  components: {
    userhead
  },
  data() {
    return {
      imgarr: [],
      telalert: false,
      advisertype: Number,
      info: {
        headimg: "",
        name: "",
        place: "",
        year: "",
        adviserId: "",
        tel: ""
      }
    };
  },
  created() {
    if (this.$route.query.type == "baoxian") {
      this.advisertype = 3;
    }
    if (this.$route.query.type == "jinji") {
      this.advisertype = 4;
    }
    if (this.$route.query.type == "shigu") {
      this.advisertype = 2;
    }
    this.getAdviserInfo();
    this.getswiper();
  },
  methods: {
    dotel() {
      this.telalert = true;
    },
    getAdviserInfo() {
      this.$axios
        .get(domain + "vehicle/typeAdvisers", {
          params: {
            type: this.advisertype
          }
        })
        .then(res => {
          let r = res.data.data;
          this.info = {
            headimg: r.headimgurl,
            name: r.name,
            place: r.poiName,
            year: r.period == 0 ? "不满一年" : "从业" + r.period + "年以上",
            adviserId: r.id,
            tel: r.mobile
          };
        })
        .catch(function(err) {
          console.log(err);
        });
    },
    recordtel(type, adviserId, peoType) {
      console.log(type, adviserId, peoType);
      this.$axios
        .post(domain + "vehicle/addConsultations", {
          type,
          adviserId,
          peoType
        })
        .then(res => {})
        .catch(function(err) {
          console.log(err);
        });
    },
     getswiper() {
     this.$axios
        .get(domain + "banner/homePage", {})
        .then(res => {
          let {
            data: { data }
          } = res;
          data.forEach(element => {
            this.imgarr.push(element.imgUrl);
          });
        })
        .catch(function(err) {
          console.log(err);
        });
    },
  }
};
</script>
<style lang="less">
.callhelp {
  text-align: center;
  color: #fff;
  h2 {
    margin-top: 40px;
  }
  .infobox {
    padding: 25px 20px 20px 20px;
    border-radius: 6px;
    background: rgba(150, 150, 150, 0.3);
    > img {
      width: 60px;
      height: 60px;
      margin-bottom: 10px;
      border-radius: 50%;
    }
    .name {
      font-size: 16px;
    }
    .shop {
      color: #8a8a8a;
      font-size: 14px;
      margin: 5px 0;
    }
    .year {
      color: #8a8a8a;
      font-size: 12px;
    }
    .twobox {
      display: flex;
      justify-content: space-between;
      margin-top: 20px;
      > div,
      > a {
        height: 40px;
        width: 47%;
        border-radius: 5px;
        border: 1px solid #f10100;
        background: #c41d24;
        color: #fff;
        font-size: 14px;
        line-height: 40px;
        text-align: center;
        i {
          font-size: 25px;
          vertical-align: top;
          line-height: 40px;
        }
      }
    }
  }
   .van-swipe {
    margin-top: 15px;
    border-radius: 8px;
    height: 150px;
    .van-swipe-item {
      background-position: center;
      background-repeat: no-repeat;
      background-size: cover;
    }
  }

  // tel弹窗
  .van-popup {
    border-radius: 5px;
    background-color: rgba(80, 80, 80, 0.9);
    padding: 30px 30px 50px;
    width: 70%;
    color: #fff;
    h3 {
      margin-top: 0;
      text-align: center;
    }
    .van-icon::before {
      color: #fff;
    }
    a {
      height: 40px;
      line-height: 40px;
      text-align: center;
      background: #c41d24;
      border-radius: 5px;
      margin-top: 20px;
      color: #fff;
      display: block;
      font-size: 16px;
    }
    p {
      text-align: center;
    }
  }
}
</style>


