<template>
  <div class="index">
    <userhead />
    <div class="wel">这里是车主服务大厅，</div>
    <div class="wel">您可以在下属功能中找到您的专属服务！</div>
    <div class="threebox">
      <router-link to="/adviser" tag="div">
        <img src="../assets/images/index/a.png" />
        <p>我的顾问</p>
      </router-link>
      <router-link to="/maintain" tag="div">
        <img src="../assets/images/index/b.png" />
        <p>预约保养</p>
      </router-link>
      <router-link :to="{path:'/callhelp',query: {type: 'baoxian'}}" tag="div">
        <img src="../assets/images/index/c.png" />
        <p>购买车险</p>
      </router-link>
    </div>
    <div class="twobox">
      <div @click="$router.push({path:'/callhelp',query: {type: 'jinji'}})">
        <img src="../assets/images/index/d.png" />
        紧急救援
        <div class="conecttype">
          <a class="imgtel" @click.stop="dotel(left.tel,left.adviserId,left.peoType)">
            <img src="../assets/images/2_03.png" class="firstimg" />
          </a>
          <img src="../assets/images/1_03.png" alt srcset />
        </div>
      </div>
      <div @click="$router.push({path:'/callhelp',query: {type: 'shigu'}})">
        <img src="../assets/images/index/e.png" />
        事故救援
        <div class="conecttype" @click.stop>
          <a class="imgtel" @click.stop="dotel(right.tel,right.adviserId,right.peoType)">
            <img src="../assets/images/2_03.png" class="firstimg" />
          </a>
          <img src="../assets/images/1_03.png" alt srcset />
        </div>
      </div>
    </div>

    <van-swipe ref="zswiper" :autoplay="3000" indicator-color="white" :show-indicators="true">
      <van-swipe-item v-for="(v,i) in imgarr" :key="i" :style="{'background-image':'url('+v+')'}">
        <!-- <img :src="v" /> -->
      </van-swipe-item>
    </van-swipe>

    <van-popup :overlay="false" :round="true" v-model="telalert" closeable>
      <h3>您的顾问电话</h3>
      <a class="teldiv" :href="'tel:'+telnumber" @click="recordtel">{{telnumber}}</a>
      <p>请直接点击拨打</p>
    </van-popup>
  </div>
</template>
<script>
import userhead from "../modal/userhead";
export default {
  name: "index",
  components: {
    userhead
  },
  data() {
    return {
      imgarr: [],
      telalert: false,
      telnumber: Number,
      left: {
        tel: "",
        adviserId: "",
        peoType: ""
      },
      right: {
        tel: "",
        adviserId: "",
        peoType: ""
      },
      ZadviserId: "",
      ZpeoType: ""
    };
  },
  created() {
    this.getswiper();
    this.getTwoAdviser();
  },
  methods: {
    // swiper
    getswiper() {
      this.$axios
        .get(domain + "banner/homePage", {})
        .then(res => {
          let {
            data: { data }
          } = res;
          // console.log(res.data);
          data.forEach(element => {
            // console.log(element.imgUrl)
            this.imgarr.push(element.imgUrl);
          });
        })
        .catch(function(err) {
          console.log(err);
        });
    },
    dotel(num, adviserId, peoType) {
      // console.log(num,peoType)
      this.telalert = true;
      this.telnumber = num;
      this.ZadviserId = adviserId;
      this.ZpeoType = peoType;
    },
    recordtel() {
      //       type: //'1电话  2在线咨询',
      // adviser_id ://顾问id
      // peo_type://顾问类型
      this.$axios
        .post(domain + "vehicle/addConsultations", {
          type: 1,
          adviserId: this.ZadviserId,
          peoType: this.ZpeoType
        })
        .then(res => {})
        .catch(function(err) {
          console.log(err);
        });
    },
    // 获取首页紧急救援和事故救援电话
    getTwoAdviser() {
      // 紧急救援
      this.$axios
        .get(domain + "vehicle/typeAdvisers", {
          params: {
            type: 4
          }
        })
        .then(res => {
          let r = res.data.data;
          this.left = {
            tel: r.mobile,
            adviserId: r.id,
            peoType: r.type.value
          };
        })
        .catch(function(err) {
          console.log(err);
        });
      // 事故救援
      this.$axios
        .get(domain + "vehicle/typeAdvisers", {
          params: {
            type: 2
          }
        })
        .then(res => {
          let r = res.data.data;
          this.right = {
            tel: r.mobile,
            adviserId: r.id,
            peoType: r.type.value
          };
        })
        .catch(function(err) {
          console.log(err);
        });
    }
  }
};
</script>
<style lang="less">
.index {
  color: #797c7b;
  // padding: 0 20px;

  .wel {
    text-align: right;
  }
  .threebox {
    margin-top: 50px;
    display: flex;
    justify-content: space-between;
    > div {
      text-align: center;
      width: 31%;
      background: #2c3036;
      border-radius: 8px;
      padding: 20px 0;
      img {
        width: 30px;
      }
      p {
        margin: 0;
      }
    }
  }
  .twobox {
    margin-top: 15px;
    display: flex;
    justify-content: space-between;
    img {
      width: 30px;
      vertical-align: middle;
      margin-right: 5px;
    }
    > div {
      width: 49%;
      text-align: center;
      background: #2c3036;
      border-radius: 8px;
      padding: 20px 0;
      .conecttype {
        margin-top: 10px;
        .imgtel {
          height: 30px;
          display: inline-block;
        }
        .firstimg {
          margin-right: 20px;
        }
      }
    }
  }
  .van-swipe {
    margin-top: 15px;
    border-radius: 8px;
    height: 150px;
    .van-swipe-item {
      background-position: center;
      background-repeat: no-repeat;
      background-size: cover;
      // img {
      //   width: 100%;
      //   height: 150px;
      //   border-radius: 8px;
      // }
    }
  }
  // tel弹窗
  .van-popup {
    border-radius: 5px;
    background-color: rgba(80, 80, 80, 0.9);
    padding: 30px 30px 50px;
    width: 70%;
    color: #fff;
    h3 {
      margin-top: 0;
      text-align: center;
    }
    .van-icon::before {
      color: #fff;
    }
    a {
      height: 40px;
      line-height: 40px;
      text-align: center;
      background: #c41d24;
      border-radius: 5px;
      margin-top: 20px;
      color: #fff;
      display: block;
      font-size: 16px;
    }
    p {
      text-align: center;
    }
  }
}
</style>


