<template>
  <div id="app" :style="{'padding-bottom':($route.meta.showFooter?'70px':'0')}">
    <transition :name="transition" mode="out-in">
      <router-view class="view" />
    </transition>
    <van-tabbar v-model="active" v-show="$route.meta.showFooter">
      <van-tabbar-item icon="like-o" to="/index">车主服务中心</van-tabbar-item>
      <van-tabbar-item icon="user-o" to="/mycenter">个人中心</van-tabbar-item>
    </van-tabbar>
  </div>
</template>

<script>
export default {
  name: "App",
  data() {
    return {
      active: 0,
      transition: "slide-right"
    };
  },
  watch: {
    $route(to, from) {
      if (to.meta.index > from.meta.index) {
        this.transition = "slide-left";
      } else {
        this.transition = "slide-right";
      }
    }
  },
  mounted() {
    // 返回到首页就不能反悔了
    if (window.history && window.history.pushState) {
      history.pushState(null, null, document.URL);
    }
  },
  methods: {}
};
</script>

<style>
@import "../static/fontcss/font.css";
#app {
  font-family: "mazidazw";
  letter-spacing: 1px;
  padding: 0 20px;
  overflow-x: hidden;
}
body {
  background: #212327 url("./assets/images/carbg.jpg");
  background-attachment: fixed;
  background-repeat: no-repeat;
  background-size: 100%;
  background-attachment: fixed;
  background-position: top left;
}
body:before {
  content: "";
  background-image: inherit;
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100%;
  -webkit-background-size: cover !important;
  -moz-background-size: cover !important;
  -o-background-size: cover;
  background-size: cover !important;
  z-index: -1;
}
.bottom {
  position: fixed;
  bottom: 0;
  display: flex;
  justify-content: space-between;
}

.van-tabbar {
  background: #b31a1f;
  height: 55px;
}
.van-hairline--top-bottom::after,
.van-hairline-unset--top-bottom::after {
  border-width: 0;
}
.van-tabbar-item {
  color: #fff !important;
  background: #b31a1f;
}
.van-tabbar-item--active {
  background: #c41d24;
}

/* router animation */
.view {
  transition: all 0.3s linear;
}
.slide-left-enter,
.slide-right-leave-active {
  opacity: 0;
  transform: translate(100%, 0);
}

.slide-left-leave-active,
.slide-right-enter {
  opacity: 0;
  transform: translate(-100%, 0);
}
.headimg {
  border-radius: 50%;
}
</style>



