<template>
  <div class="coupon">
    <div class="advul">
      <div class="advli" v-for="(v,i) in CouponList" :key="i">
        <div class="percent" v-show="v.type.text=='折扣券'">
          {{v.discount}}
          <span class="zhe">折</span>
        </div>
        <div class="percent" v-show="v.type.text=='代金券'">
          <span class="yuan">￥</span>
          {{v.money}}
        </div>
        <div class="text">
          <div>{{v.name}}</div>
          <div>{{v.startTime.substring(0,10)}}-{{v.endTime.substring(0,10)}}</div>
          <div class="but" @click="showdetail(v.cardId)">详情</div>
        </div>
      </div>
    </div>

    <van-popup v-model="detail" closeable>
      <div class="up">
        <div class="aa">
          {{detailobj.discount}}
          <span>折</span>
        </div>
        <div class="bb">{{detailobj.name}}</div>
        <div
          class="cc"
        >有效期:{{detailobj.startTime.substring(0,10)}}-{{detailobj.endTime.substring(0,10)}}</div>
        <div class="dd">
          <img src="../assets/images/tiaoxingma.jpg" alt srcset />
        </div>
        <div class="ee">优惠券号:12390974981623960</div>
      </div>
      <div class="hr"></div>
      <div class="down">
        <div class="ff">
          <div>优惠券类型</div>
          <div>{{detailobj.type.text}}</div>
        </div>
        <div class="ff">
          <div>可用范围</div>
          <div>长春门店</div>
        </div>
        <div class="ff">
          <div>限制条件</div>
          <div>S4店车型可用</div>
        </div>
      </div>
    </van-popup>
  </div>
</template>
<script>
export default {
  name: "coupon",
  data() {
    return {
      detail: false,
      CouponList: [],
      detailobj: {
        startTime: "",
        endTime: "",
        type: {
          text: ""
        }
      }
    };
  },
  created() {
    this.getCouponList();
  },
  methods: {
    getCouponList() {
      this.$axios
        .get(domain + "vehicle/cards", {})
        .then(res => {
          res.data.data.forEach(e => {
            this.CouponList.push(e);
          });
        })
        .catch(function(err) {
          console.log(err);
        });
    },
    showdetail(id) {
      this.detail = true;
      this.$axios
        .get(domain + "vehicle/cards/detail", {
          params: {
            cardId: id
          }
        })
        .then(res => {
          this.detailobj = res.data.data;
        })
        .catch(function(err) {
          console.log(err);
        });
    }
  }
};
</script>
<style lang="less">
.coupon {
  color: #fff;
  .advul {
    margin-top: 70px;
    .advli {
      border-radius: 6px;
      background: rgba(150, 150, 150, 0.3);
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 15px;
      height: 100px;
      .percent {
        height: 100px;
        line-height: 100px;
        width: 40%;
        font-size: 28px;
        position: relative;
        text-align: center;
        span {
          font-size: 13px;
          &.zhe {
            margin-left: -8px;
          }
          &.yuan {
            margin-right: -8px;
          }
        }
        &::after {
          content: "";
          position: absolute;
          width: 1px;
          height: 100%;
          right: 0;
          top: 0;
          border-right: 1px dashed #9a9a9a;
        }
      }
      .text {
        width: 58%;
        > div:first-child {
          color: #fff;
          font-size: 16px;
          margin-bottom: 5px;
        }
        > div:nth-child(2) {
          font-size: 12px;
          color: #9a9a9a;
        }
      }
      .but {
        width: 35px;
        margin-right: 10px;
        height: 28px;
        padding: 0 18px;
        border-radius: 5px;
        border: 1px solid #f10100;
        background: #c41d24;
        color: #fff;
        font-size: 13px;
        line-height: 28px;
        text-align: center;
        margin-top: 5px;
      }
    }
  }

  .van-popup {
    background: linear-gradient(to bottom right, #747f83, #c41d24);
    text-align: center;
    padding: 30px 0;
    width: 65%;
    .up {
      margin: 0 10px;

      .aa {
        font-size: 45px;
        font-weight: bold;
        span {
          font-size: 14px;
          font-weight: normal;
        }
      }
      .bb {
        font-size: 16px;
        margin: 5px 0;
      }
      .cc {
        margin-bottom: 30px;
      }
      .dd {
        img {
          width: 90%;
        }
      }
      .ee {
        color: #bbb;
        position: relative;
        margin: 5px 0 20px;
      }
    }
    .hr {
      width: 100%;
      height: 1px;
      background: #bbb;
    }
    .down {
      padding: 10px 20px;
      .ff {
        display: flex;
        justify-content: space-between;
        > div:first-child {
          color: #bbb;
        }
      }
    }
    .van-icon-cross:before {
      color: white;
      font-size: 23px;
    }
  }
}
</style>


