<template>
  <div class="adviser">
    <userhead />
    <div class="advul">
      <div class="advli" v-for="(v,i) in adviserList" :key="i">
        <img :src="v.imgurl" class="headimg" />
        <div class="text">
          <div>{{v.name}}</div>
          <div>{{v.poiName}}</div>
          <div>
            <span>{{v.text}}</span>
            {{v.period}}
          </div>
        </div>
        <div class="contype">
          <a @click="dotel(v.tel,v.adviserId,v.peoType)">
            <img src="../assets/images/2_03.png" alt srcset />
          </a>
          <img src="../assets/images/1_03.png" alt srcset />
        </div>
      </div>
    </div>
    <div class="ultitle">
      <img src="../assets/images/history.png" alt srcset />
      资讯历史
    </div>

    <van-loading type="spinner" v-show="showload" style="text-align:center;margin-bottom:10px;" />
    <van-list
      v-show="!showload"
      class="historyul"
      v-model="list.loading"
      :finished="list.finished"
      finished-text="没有更多了"
      @load="onLoad()"
      :immediate-check="false"
    >
      <div class="historyli" v-for="(v,i) in list.historyList" :key="i">
        <div class="datetime">
          {{v.createTime.substring(0,4)}}
          <span>{{v.createTime.substring(5,10)}}</span>
          {{v.createTime.substring(11,16)}}
        </div>
        <div class="asktype">{{v.type.text}}</div>
        <div class="memtype">
          <span class="nodetail" v-if="v.type.text!='在线咨询'">服务顾问</span>
          <span class="detail" v-if="v.type.text=='在线咨询'">详情</span>
        </div>
      </div>
    </van-list>

    <van-popup :overlay="false" :round="true" v-model="telalert" closeable>
      <h3>您的顾问电话</h3>
      <a
        class="teldiv"
        :href="'tel:'+telnumber"
        @click="recordtel(1,adviserId,peoType)"
      >{{telnumber}}</a>
      <p>请直接点击拨打</p>
    </van-popup>
  </div>
</template>
<script>
import userhead from "../modal/userhead";
// import http from "../request/axiosConfig";
export default {
  name: "adviser",
  components: {
    userhead
  },
  data() {
    return {
      adviserList: [],
      pageIndex: 0, // 页码
      list: {
        historyList: [],
        loading: false,
        finished: false
      },
      showload: true,
      telalert: false,
      telnumber: Number,
      adviserId: "",
      peoType: ""
    };
  },
  created() {
    this.getAdvisterList();
    this.getHistoryList();
  },
  methods: {
    getAdvisterList() {
      this.$axios
        .get(domain + "vehicle/myAdvisers", {})
        .then(res => {
          res.data.data.forEach(e => {
            this.adviserList.push({
              name: e.name,
              poiName: e.poiName,
              text: e.type.text,
              period: e.period == 0 ? "不满一年" : "从业" + e.period + "年以上",
              tel: e.mobile,
              imgurl: e.headimgurl,
              adviserId:e.id,
              peoType:e.type.value
            });
          });
        })
        .catch(function(err) {
          console.log(err);
        });
    },
    getHistoryList() {
      const _this = this;
      this.$axios
        .post(domain + "vehicle/consultation", {
          limit: 10,
          offset: this.pageIndex,
          sort: "createTime",
          order: "desc",
          filterRules: "openid_equal_" + openid
        })
        .then(res => {
          if (this.pageIndex == 0) {
            this.showload = !this.showload;
          }
          // console.log(res.data.rows.length);
          if (res.data.rows.length == 0) {
            _this.list.finished = true;
            return;
          }
          res.data.rows.forEach(e => {
            _this.list.historyList.push(e);
          });
        })
        .catch(function(err) {
          console.log(err);
        })
        .finally(function() {
          _this.list.loading = false;
          _this.list.refreshing = false;
        });
    },
    onLoad() {
      const list = this.list;
      const _this = this;
      list.loading = true;
      _this.pageIndex++;
      console.log(_this.pageIndex);
      _this.getHistoryList();
    },
    dotel(num, adviserId, peoType) {
      this.telalert = true;
      this.telnumber = num;
      this.adviserId = adviserId;
      this.peoType = peoType;
    },
    recordtel(type, adviserId, peoType) {
      // type: //'1电话  2在线咨询',
      // adviser_id ://顾问id
      // peo_type://顾问类型
      this.$axios
        .post(domain + "vehicle/addConsultations", {
          type,
          adviserId: this.adviserId,
          peoType: this.peoType
        })
        .then(res => {})
        .catch(function(err) {
          console.log(err);
        });
    }
  }
};
</script>
<style lang="less">
.adviser {
  color: #797c7b;
  .advul {
    .advli {
      border-radius: 6px;
      background: rgba(150, 150, 150, 0.3);
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 10px;
      margin-bottom: 15px;
      .headimg {
        width: 40px;
        height: 40px;
        border-radius: 50%;
      }
      .text {
        width: 60%;
        > div:first-child {
          color: #fff;
          font-size: 16px;
        }
        > div:nth-child(2) {
          font-size: 13px;
          //   margin: 5px 0;
        }
        > div:nth-child(3) {
          font-size: 11px;
          line-height: 20px;
          span {
            background: #c41d24;
            border-radius: 8px;
            color: #fff;
            padding: 0 10px;
          }
        }
      }
      .contype {
        img {
          width: 30px;
          display: block;
        }
        img:first-child {
          margin-bottom: 10px;
        }
      }
    }
  }
  .ultitle {
    color: #fff;
    font-size: 14px;
    img {
      width: 20px;
      vertical-align: middle;
      margin-right: 5px;
    }
  }
  .historyul {
    margin: 10px 0;
    border-radius: 6px;
    background: rgba(150, 150, 150, 0.3);
    height: 367px;
    overflow-y: scroll;
    .historyli {
      height: 45px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 0 10px;
      border-bottom: 1px solid #5b5e68;
      &:last-child {
        border-bottom-width: 0;
        position: relative;
      }
      &:last-child:after {
        content: "";
        width: 100%;
        height: 1px;
        background: linear-gradient(
          to right,
          rgba(255, 0, 0, 0) 25%,
          #ff0000 48%,
          #c6c9cb 50%,
          #ff0000 53%,
          rgba(255, 0, 0, 0) 75%
        );
        position: absolute;
        bottom: 0;
        left: 0%;
      }
      .datetime {
        span {
          color: #fff;
          font-size: 13px;
        }
      }
      .asktype {
        a {
          color: #797c7b;
        }
        font-size: 14px;
      }
      .memtype {
        width: 68px;
        text-align: center;
        .nodetail {
          font-size: 14px;
          color: #fff;
        }
        .detail {
          border-radius: 5px;
          padding: 4px 10px;
          border: 1px solid #ccc;
          color: #ccc;
        }
      }
    }
  }

  // tel弹窗
  .van-popup {
    border-radius: 5px;
    background-color: rgba(80, 80, 80, 0.9);
    padding: 30px 30px 50px;
    width: 70%;
    color: #fff;
    h3 {
      margin-top: 0;
      text-align: center;
    }
    .van-icon::before {
      color: #fff;
    }
    a {
      height: 40px;
      line-height: 40px;
      text-align: center;
      background: #c41d24;
      border-radius: 5px;
      margin-top: 20px;
      color: #fff;
      display: block;
      font-size: 16px;
    }
    p {
      text-align: center;
    }
  }
}
</style>


