<template>
  <div class="evaluation">
    <div class="advli">
      <img src="../assets/images/ueserbind-headimg.png" class="headimg" />
      <div class="text">
        <div>王小明</div>
        <div>长安马自达专属4S店</div>
        <div>
          <span>专属服务顾问</span>
          从业4年以上
        </div>
      </div>
    </div>
    <div class="stardiv">
      <div>
        整体印象
        <van-rate
          v-model="value1"
          :size="22"
          color="#c41d24"
          void-icon="star"
          void-color="#4d5158"
        />
        <span v-show="value1==1">非常不满意</span>
        <span v-show="value1==2">不满意</span>
        <span v-show="value1==3">一般</span>
        <span v-show="value1==4">满意</span>
        <span v-show="value1==5">非常满意</span>
        <div>
          服务专员
          <van-rate
            v-model="value2"
            :size="22"
            color="#c41d24"
            void-icon="star"
            void-color="#4d5158"
          />
          <span v-show="value2==1">非常不满意</span>
          <span v-show="value2==2">不满意</span>
          <span v-show="value2==3">一般</span>
          <span v-show="value2==4">满意</span>
          <span v-show="value2==5">非常满意</span>
        </div>
      </div>
    </div>
    <van-cell title="不满意原因" :value="reason" is-link @click="showPicker = true" />
    <!-- 不满意原因 -->
    <van-popup v-model="showPicker" position="bottom">
      <van-picker show-toolbar :columns="ordertypecolumns" @cancel="onCancel" @confirm="onConfirm" />
    </van-popup>

    <div class="survice">
      <div>其他建议</div>
      <van-field v-model="message" type="textarea" rows="3" autosize />
    </div>
  </div>
</template>
<script>
export default {
  name: "evaluation",
  data() {
    return {
      value1: 3,
      value2: 3,
      reason: "请选择",
      showPicker: false,
      ordertypecolumns: ["原因1", "原因2"],
      message: ""
    };
  },
  methods: {
    onConfirm(value, index) {
      //  console.log(`当前值：${value}, 当前索引：${index}`);
      this.showPicker = false;
      this.reason = value;
    },
    onCancel() {
      this.showPicker = false;
    }
  }
};
</script>
<style lang="less">
.evaluation {
  .advli {
    color: #8a8a8a;
    border-radius: 6px 6px 0 0;
    background: rgba(150, 150, 150, 0.3);
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 15px;
    margin-top: 60px;
    border-bottom: 1px solid #8a8a8a;
    .headimg {
      width: 40px;
      margin-right: 25px;
    }
    .text {
      > div:first-child {
        color: #fff;
        font-size: 16px;
      }
      > div:nth-child(2) {
        font-size: 13px;
        //   margin: 5px 0;
      }
      > div:nth-child(3) {
        font-size: 11px;
        line-height: 20px;
        span {
          background: #c41d24;
          border-radius: 8px;
          color: #fff;
          padding: 0 10px;
        }
      }
    }
  }
  .stardiv {
    color: #fff;
    border-radius: 0 0 6px 6px;
    background: rgba(150, 150, 150, 0.3);
    text-align: center;
    padding: 15px;
    line-height: 25px;
    font-size: 14px;
    span {
      font-size: 12px;
      width: 70px;
      display: inline-block;
    }
    .van-rate {
      vertical-align: top;
      margin-left: 10px;
    }
  }
  .van-cell {
    border-radius: 6px;
    background: rgba(150, 150, 150, 0.3);
    color: #fff;
    margin: 15px 0;
  }
  .van-picker__cancel,
  .van-picker__confirm {
    color: #c41d24;
  }
  .van-cell:not(:last-child)::after {
    border-bottom-width: 0;
  }

  .survice {
    border-radius: 6px;
    background: rgba(150, 150, 150, 0.3);
    color: #fff;
    padding: 15px;
    font-size: 14px;
  }

  .van-field {
    border: 1px solid #8a8a8a;
    background: rgba(150, 150, 150, 0);
  }
  .van-field__control {
    color: #fff;
  }
}
</style>


