<template>
  <div class="userbind">
    <div class="zbox1">
      <img src="../assets/images/ueserbind-headimg.png" />
      车主绑定
    </div>
    <div class="zbox zbox2">
      <img src="../assets/images/userbind-phone.png" />
      <van-field v-model="moblie" placeholder="请输入手机号" type="number" />
    </div>

    <div class="zbox3">
      <div class="zbox">
        <img src="../assets/images/userbind-code.png" />
        <van-field v-model="javacode" placeholder="图形验证码" type="text" />
      </div>
      <div>
        <img class="imgcode" :src="imageUrl" @click="getImageUrl" />
      </div>
    </div>
    <div class="zbox3">
      <div class="zbox">
        <img src="../assets/images/userbind-code.png" />
        <van-field v-model="code" placeholder="手机验证码" type="number" />
      </div>
      <div class="codetxt" type="button" @click="sendcode">{{btntxt}}</div>
    </div>
    <div class="zbox4">
      <van-checkbox v-model="checked" checked-color="#c41d24">
        我已同意绑定协议
        <span @click="showAgreement">《一汽马自达车主服务大厅用户协议》</span>
      </van-checkbox>
    </div>
    <div class="zbox5" @click="login">登录</div>

    <van-popup :overlay="true" :round="true" v-model="Agreement" closeable v-if="tab==1">
      <h3>服务条款</h3>
      <span>一汽马自达汽车销售有限公司目前主营以下几款系列轿车的销售及售后服务，分别是Mazda6阿特兹、CX-4以及它们的零部件、维修工</span>
    </van-popup>

    <van-popup :overlay="true" :round="true" v-model="Agreement" closeable v-if="tab==2">
      <div class="icondiv">
        <van-icon name="smile-o" />
      </div>
      <h3>系统暂未查到您的信息</h3>
      <span>
        如果您是新购车，请在24小时后再进行尝试，如果您是老车主（购车时间超过一周），请点此联系您的客户顾问
        <a href="tel:400-666-8080">400-666-8080</a>
      </span>
    </van-popup>

    <van-popup :overlay="true" :round="true" v-model="Agreement" v-if="tab==3">
      <div class="icondiv">
        <van-icon name="passed" />
      </div>
      <h3>绑定成功</h3>
      <span>您是非本店专属车主，绑定成功，但您无法享受您专属门店的服务哦</span>
      <div class="closepopup" @click="Agreement = false,$router.push('index')">我已知</div>
    </van-popup>

    <van-popup :overlay="true" :round="true" v-model="Agreement" v-if="tab==4">
      <div class="icondiv">
        <van-icon name="passed" />
      </div>
      <h3>登录成功</h3>
      <div style="text-align:center">恭喜您，您是本店的专属车主</div>
    </van-popup>
  </div>
</template> 

<script>
export default {
  name: "userbind",
  data() {
    return {
      moblie: "",
      javacode: "",
      code: "",
      btntxt: "发送验证码",
      disabled: false,
      time: 0,
      checked: false,
      Agreement: false,
      tab: 1,
      imageUrl: ""
    };
  },
  created() {
    this.getImageUrl();
  },
  methods: {
    // 更新图片
    getImageUrl() {
      var date = new Date();
      var time = date.getTime();
      var captcha = time + "-" + Math.floor(Math.random() * 1000);
      this.imageUrl =
        domain + "code/imgCode?openId=" + openid + "&captcha=" + captcha;
      // console.log(this.imageUrl);
    },
    sendcode() {
      var reg = 11 && /^((13|14|15|17|18)[0-9]{1}\d{8})$/;
      if (this.moblie == "") {
        this.$toast("手机号不能为空");
      } else if (!reg.test(this.moblie)) {
        this.$toast("手机号格式不正确");
      } else if (this.javacode == "") {
        this.$toast("图形验证码不能为空");
      } else if (this.disabled == true) {
        return;
      } else {
        this.disabled = true;
        this.time = 60;
        this.timer();
        //手机号争取后调取接口
        this.$axios
          .get(domain + "sms/sendSms", {
            params: {
              mobile: this.moblie,
              operType: "GZH"
            }
          })
          .then(res => {
            console.log(res);
            if (res.status != 200) {
              this.$toast("网络错误,请稍后再试");
            } else if (res.status == 200) {
              this.$toast("请注意接收验证码");
            }
          })
          .catch(function(err) {
            console.log(err);
          });
      }
    },
    //验证码的倒计时
    timer() {
      if (this.time > 0) {
        this.time--;
        this.btntxt = this.time + "s后获取";
        setTimeout(this.timer, 1000);
      } else {
        this.time = 0;
        this.btntxt = "发送验证码";
        this.disabled = false;
      }
    },
    // showAgreement laert
    showAgreement() {
      window.event ? (window.event.cancelBubble = true) : e.stopPropagation();
      this.Agreement = true;
    },
    login() {
      if (this.mobile == "") {
        this.$toast("手机号不能为空");
        return;
      }
      if (this.javacode == "") {
        this.$toast("图形验证码不能为空");
        return;
      }
      if (this.code == "") {
        this.$toast("手机验证码不能为空");
        return;
      }
      if (this.checked == "") {
        this.$toast("请先勾选用户协议");
        return;
      }
      this.$axios
        .post(domain + "vehicle/login", {
          mobile: this.moblie,
          smsCode: this.code,
          code: this.javacode
        })
        .then(res => {
          if (res.data.code == 701) {
            this.tab = 2;
            this.Agreement = true;
          }
          if (res.data.code == 702) {
            this.tab = 3;
            this.Agreement = true;
          }
          if (res.data.code == 200) {
            this.tab = 4;
            this.Agreement = true;
            setTimeout(() => {
              this.$router.push("index");
            }, 1000);
          }
          if (
            res.data.code == 209 ||
            res.data.code == 208 ||
            res.data.code == 207 ||
            res.data.code == 206
          ) {
            this.$toast(res.data.message);
          }
        })
        .catch(function(err) {
          console.log(err);
        });
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
.userbind {
  // width: 85%;
  margin: 185px auto 0;
  color: #fff;
  .zbox1 {
    line-height: 40px;
    font-size: 16px;
    font-weight: bold;
    img {
      vertical-align: middle;
      width: 40px;
      margin-right: 20px;
    }
  }
  .zbox2 {
    margin-top: 0.4rem;
  }
  .zbox {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    height: 40px;
    background: rgba(255, 255, 255, 0.04);
    border-radius: 5px;
    padding-left: 15px;
    img {
      width: 15px;
      float: left;
    }
    .van-cell {
      background-color: rgba(0, 0, 0, 0);
    }
  }
  .zbox3 {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 0.4rem;
    > div:last-child {
      margin-left: 5px;
      width: 105px;
    }
    .codetxt {
      background: #c41d24;
      border-radius: 5px;
      height: 40px;
      line-height: 40px;
      text-align: center;
    }
    .imgcode {
      height: 32px;
      vertical-align: middle;
    }
  }
  .zbox4 {
    width: 120%;
    margin-top: 35px;
  }
  .zbox5 {
    height: 40px;
    line-height: 40px;
    letter-spacing: 20px;
    text-align: center;
    // background: #36393b;
    background: #c41d24;
    margin-top: 20px;
    font-size: 16px;
    padding-left: 20px;
    border-radius: 5px;
  }
  .van-popup {
    h3 {
      margin-top: 0;
    }
    .icondiv {
      text-align: center;
      .van-icon:before {
        font-size: 65px;
      }
    }
    .closepopup {
      height: 40px;
      line-height: 40px;
      text-align: center;
      background: #c41d24;
      border-radius: 5px;
      margin-top: 20px;
    }
  }
}
</style>
<style lang="less">
.userbind {
  .van-field__control {
    color: #eee;
  }
  .van-cell:not(:last-child)::after {
    border-width: 0;
  }
  // checkbox
  .van-checkbox__icon--round .van-icon {
    border-radius: 0;
    border: 0.026667rem solid #666;
  }
  .van-checkbox__label {
    color: #767676;
    transform: scale(0.9);
    margin-left: -10px;
  }

  // 协议弹窗样式
  .van-popup--center.van-popup--round {
    border-radius: 5px;
    background-color: rgba(80, 80, 80, 0.9);
    padding: 30px 30px 50px;
    width: 70%;
    color: #fff;
    h3 {
      text-align: center;
    }
    .van-icon,
    .van-icon::before {
      color: #fff;
    }
  }
  a {
    color: #fff;
  }
}
</style>



