import Vue from 'vue'
import Router from 'vue-router'
import userbind from '@/components/userbind'
import index from '@/components/index'
import mycenter from '@/components/mycenter'
import adviser from '@/components/adviser'
import maintain from '@/components/maintain'
import addorder from '@/components/addorder'
import historyswiper from '@/components/historyswiper'
import callhelp from '@/components/callhelp'
import heartfriend from '@/components/heartfriend'
import coupon from '@/components/coupon'
import evaluation from '@/components/evaluation'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',  // 默认进入路由
      redirect: '/index'   //重定向
    },
    {
      path: '/userbind',
      name: 'userbind',
      component: userbind,
      meta: {
        showFooter: false,
        index: 1
      }
    }, {
      path: '/mycenter',
      name: 'mycenter',
      component: mycenter,
      meta: {
        showFooter: true,
        index: 1
      }
    },
    {
      path: '/index',
      name: 'index',
      component: index,
      meta: {
        showFooter: true,
        index: 1
      }
    },
    {
      path: '/adviser',
      name: 'adviser',
      component: adviser,
      meta: {
        showFooter: true,
        index: 2
      }
    },
    {
      path: '/maintain',
      name: 'maintain',
      component: maintain,
      meta: {
        showFooter: true,
        index: 2
      }
    }, {
      path: '/addorder',
      name: 'addorder',
      component: addorder,
      meta: {
        showFooter: true,
        index: 3
      }
    },
    {
      path: '/historyswiper',
      name: 'historyswiper',
      component: historyswiper,
      meta: {
        showFooter: true,
        index: 3
      }
    },
    {
      path: '/callhelp',
      name: 'callhelp',
      component: callhelp,
      meta: {
        showFooter: true,
        index: 2
      }
    }, {
      path: '/heartfriend',
      name: 'heartfriend',
      component: heartfriend,
      meta: {
        showFooter: true,
        index: 2
      }
    }, {
      path: '/coupon',
      name: 'coupon',
      component: coupon,
      meta: {
        showFooter: true,
        index: 2
      }
    }, {
      path: '/evaluation',
      name: 'evaluation',
      component: evaluation,
      meta: {
        showFooter: true,
        index: 2
      }
    }
  ]
})



