<template>
  <div class="maintain">
    <userhead />
    <div class="zbox1" v-if="repairtype==1">
      <div>
        <div>2019-09-1</div>
        <div>建议保养时间</div>
      </div>
      <div>
        <div>
          20000
          <span>公里</span>
        </div>
        <div>建议保养里程</div>
      </div>
    </div>
    <div class="zbox1a" v-if="repairtype==2">
      <img src="../assets/images/yuyuea.png" class="yuyueimg" />
      <div>
        <div>您的爱车已经很久没有保养了</div>
        <div>快来预约保养吧，点击下面的立即预约</div>
      </div>
    </div>
    <div class="zbox1a" v-if="repairtype==3">
      <img src="../assets/images/yuyueb.png" class="yuyueimg" />
      <div>
        <div>您的爱车保养时间已经过期了</div>
        <div>快来预约保养吧，点击下面的立即预约</div>
      </div>
    </div>
    <div class="zbox2">
      <router-link to="addorder" class="repair" tag="div">立即预约保养/维修</router-link>
      <router-link to="historyswiper" class="history" tag="div">预约历史</router-link>
    </div>
    <div class="ultitle">
      <img src="../assets/images/index/b.png" alt srcset />
      保养记录
    </div>
    <van-list
      class="historyul"
      v-model="list.loading"
      :finished="list.finished"
      finished-text="没有更多了"
      @load="onLoad()"
      :immediate-check="false"
    >
      <div class="historyli">
        <div>保养时间</div>
        <div>保养里程</div>
        <div>服务门店</div>
      </div>
      <div class="historyli" v-for="(v,i) in 17" :key="i">
        <div>2019年1月1日</div>
        <div>10000公里</div>
        <div>长存瑞昌4S店</div>
      </div>
    </van-list>
  </div>
</template>
<script>
import userhead from "../modal/userhead";
export default {
  name: "maintain",
  components: {
    userhead
  },
  data() {
    return {
      repairtype: 3,
      pageIndex: 0, // 页码
      list: {
        historyList: [],
        loading: false,
        finished: false
      }
    };
  },
  created() {},
  methods: {
    getAdvisterList() {
      this.$axios
        .get(domain + "vehicle/16/advisers", {})
        .then(res => {
          res.data.data.forEach(e => {
            this.adviserList.push({
              name: e.name,
              poiName: e.poiName,
              text: e.type.text,
              period: e.period == 0 ? "不满一年" : "从业" + e.period + "年以上",
              tel: "tel:" + e.mobile,
              imgurl: require("../assets/images/ueserbind-headimg.png")
            });
          });
        })
        .catch(function(err) {
          console.log(err);
        });
    },
    getHistoryList() {
      const _this = this;
      this.$axios
        .post(domain + "vehicle/consultation", {
          limit: 10,
          offset: this.pageIndex,
          sort: "createTime",
          order: "desc",
          filterRules: "openid_equal_123"
        })
        .then(res => {
          // console.log(res.data.rows.length);
          if (res.data.rows.length == 0) {
            _this.list.finished = true;
            return;
          }
          res.data.rows.forEach(e => {
            _this.list.historyList.push(e);
          });
        })
        .catch(function(err) {
          console.log(err);
        })
        .finally(function() {
          _this.list.loading = false;
          _this.list.refreshing = false;
        });
    },
    onLoad() {
      const list = this.list;
      const _this = this;
      list.loading = true;
      _this.pageIndex++;
      console.log(_this.pageIndex);
      _this.getHistoryList();
    }
  }
};
</script>
<style lang="less">
.maintain {
  .zbox1 {
    border-radius: 6px;
    background: rgba(150, 150, 150, 0.3);
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: 25px 10px;
    margin-bottom: 15px;
    color: #fff;
    font-size: 18px;
    text-align: center;
    > div {
      width: 50%;
      position: relative;
      &:first-child:after {
        content: "";
        height: 100%;
        width: 1px;
        background: linear-gradient(
          to bottom,
          rgba(0, 0, 0, 0),
          #aaa 50%,
          rgba(0, 0, 0, 0)
        );
        position: absolute;
        top: 0;
        right: 0;
      }
      span {
        font-size: 12px;
        color: #8a8a87;
        margin-left: 5px;
      }
      > div:last-child {
        font-size: 12px;
        line-height: 25px;
      }
    }
  }
  .zbox1a {
    border-radius: 6px;
    background: rgba(150, 150, 150, 0.3);
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: 25px 10px;
    margin-bottom: 15px;
    color: #fff;
    font-size: 16px;
    text-align: left;
    img {
      width: 40px;
    }
    > div {
      > div:last-child {
        font-size: 12px;
      }
    }
  }
  .zbox2 {
    display: flex;
    justify-content: space-between;
    text-align: center;
    color: #fff;
    font-size: 14px;
    line-height: 40px;
    .repair {
      height: 40px;
      width: 48%;
      border-radius: 5px;
      border: 1px solid #f10100;
      background: #c41d24;
    }
    .history {
      height: 40px;
      width: 48%;
      border-radius: 5px;
      border: 1px solid #5b5f66;
      background: #2e3236;
    }
  }
  .ultitle {
    color: #fff;
    font-size: 14px;
    margin-top: 40px;
    img {
      width: 20px;
      vertical-align: middle;
      margin-right: 5px;
    }
  }
  .historyul {
    margin: 10px 0;
    border-radius: 8px;
    background: rgba(150, 150, 150, 0.3);
    position: relative;
    height: 360px;
    overflow-y: scroll;
    .historyli {
      height: 44px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 0px 10px;
      border-bottom: 1px solid #5b5e68;
      font-size: 13px;
      color: #fff;
      &:first-child {
        color: #8a8b88;
        font-weight: bold;
      }
      &:last-child {
        border-bottom-width: 0;
        // background-color: red !important;
      }
    }
    // &:after {
    //   content: "";
    //   width: 90%;
    //   height: 1px;
    //   background: linear-gradient(
    //     to right,
    //     rgba(255, 0, 0, 0) 25%,
    //     #ff0000 48%,
    //     #c6c9cb 50%,
    //     #ff0000 53%,
    //     rgba(255, 0, 0, 0) 75%
    //   );
    //   position: absolute;
    //   bottom: 0;
    //   left: 5%;
    // }
  }
}
</style>


