<template>
  <div class="addorder">
    <userhead />
    <div class="driverinfo">
      <div class="infoleft">
        <div>车主姓名</div>
        <div>车主手机</div>
        <div>车牌信息</div>
        <div>上次进店里程</div>
      </div>
      <div class="inforight">
        <div>{{driverInfo.name}}</div>
        <div>{{driverInfo.tel}}</div>
        <div>{{driverInfo.license}}</div>
        <div>{{driverInfo.mileage}}公里</div>
      </div>
    </div>

    <van-cell-group>
      <van-cell title="预约类型" :value="orderdata.type" is-link @click="showordertypePicker = true" />
      <van-cell title="预约来店日期" :value="orderdata.date" is-link @click="showdatePicker = true" />
      <van-cell title="预约来店时间" :value="orderdata.time" is-link @click="showtimePicker = true" />
      <van-field
        v-model="orderdata.long"
        placeholder="输入公里数"
        label="行驶里程"
        type="number"
        @input="longchange"
      />
    </van-cell-group>

    <div class="sub" @click="doSubmit">提交</div>
    <!-- 预约类型 -->
    <van-popup v-model="showordertypePicker" position="bottom">
      <van-picker show-toolbar :columns="ordertypecolumns" @cancel="onCancel" @confirm="onConfirm" />
    </van-popup>
    <!-- 预约来店日期 -->
    <van-popup v-model="showdatePicker" position="bottom">
      <van-datetime-picker
        type="date"
        :min-date="minDate"
        :max-date="maxDate"
        @cancel="ondateCancel"
        @confirm="ondateConfirm"
      />
    </van-popup>
    <!-- 预约来店时间 -->
    <van-popup v-model="showtimePicker" position="bottom">
      <van-datetime-picker type="time" @cancel="ontimeCancel" @confirm="ontimeConfirm" />
    </van-popup>
  </div>
</template>
<script>
import userhead from "../modal/userhead";
export default {
  name: "addorder",
  components: {
    userhead
  },
  data() {
    return {
      showordertypePicker: false,
      ordertypecolumns: ["预约保养", "预约维修"],
      showdatePicker: false,
      showtimePicker: false,
      minDate: new Date(new Date().setDate(new Date().getDate() + 1)),
      maxDate: new Date(new Date().getFullYear() + 2, 10, 1),
      orderdata: {
        type: "选择预约类型",
        date: "选择日期",
        time: "选择时间",
        long: "输入公里数"
      },
      driverInfo: {
        name: "",
        tel: "",
        license: "",
        mileage: ""
      }
    };
  },
  created() {
    this.getDriverInfo();
  },
  methods: {
    onConfirm(value, index) {
      //  console.log(`当前值：${value}, 当前索引：${index}`);
      this.showordertypePicker = false;
      this.orderdata.type = value;
    },
    onCancel() {
      this.showordertypePicker = false;
    },
    ondateConfirm(value) {
      var formatdate =
        new Date(value).getFullYear() +
        "-" +
        (new Date(value).getMonth() + 1) +
        "-" +
        new Date(value).getDate();
      this.orderdata.date = formatdate;
      // console.log(this.orderdata.date);
      this.showdatePicker = false;
    },
    ondateCancel() {
      this.showdatePicker = false;
    },
    ontimeConfirm(value) {
      // console.log(value);
      this.orderdata.time = value;
      this.showtimePicker = false;
    },
    ontimeCancel() {
      this.showtimePicker = false;
    },
    longchange(value) {
      // console.log(value)
      if (value < 0) {
        this.orderdata.long = 0;
      }
      if (value > 600000) {
        this.orderdata.long = 600000;
      }
    },
    doSubmit() {
      if (
        this.orderdata.type != "选择预约类型" &&
        this.orderdata.date != "选择日期" &&
        this.orderdata.time != "选择时间" &&
        this.orderdata.long != "输入公里数"
      ) {
        this.$axios
          .post(domain + "appointment/add", {
            type: 1,
            orderTime: this.orderdata.date + " " + this.orderdata.time + ":00",
            mileage: this.orderdata.long
          })
          .then(res => {
            if (res.data.code == 200) {
              this.$toast("预约成功");
              setTimeout(() => {
                this.$router.push({
                  path: "/maintain"
                });
              }, 1000);
            } else {
              this.$toast("预约失败，请稍后重试");
            }
          })
          .catch(function(err) {
            console.log(err);
          });
      } else {
        this.$toast("信息不完全");
      }
    },
    getDriverInfo() {
      let _this = this;
      this.$axios
        .get(domain + "vehicle/detail", {})
        .then(res => {
          this.driverInfo = {
            name: res.data.data.name,
            tel: res.data.data.mobile,
            license: res.data.data.licenseCode,
            mileage: res.data.data.mileage
          };
        })
        .catch(function(err) {
          console.log(err);
        });
    }
  }
};
</script>
<style lang="less">
.addorder {
  color: #8a8a87;
  font-size: 13px;
  .driverinfo {
    display: flex;
    justify-content: flex-start;
    border-radius: 6px;
    background: rgba(150, 150, 150, 0.3);
    padding: 15px 10px 15px 40px;
    .infoleft {
      text-align: right;
      margin-right: 10px;
    }
    .inforight {
      color: #fff;
      text-align: left;
    }
  }
  .sub {
    height: 40px;
    width: 48%;
    border-radius: 5px;
    border: 1px solid #f10100;
    background: #c41d24;
    color: #fff;
    font-size: 14px;
    line-height: 40px;
    text-align: center;
    letter-spacing: 20px;
    margin: 20px auto 0;
    padding-left: 20px;
  }
  .van-cell-group {
    border-radius: 6px;
    margin-top: 20px;
    overflow: hidden;
    background: #2c3036;
    .van-cell {
      background: #2c3036;
      color: #fff;
    }
    .van-cell:not(:last-child)::after {
      left: 0;
      border-bottom: 1px solid #5b5e68;
    }
    .van-field__control {
      text-align: right;
      color: #fff;
    }
  }
  .van-picker__cancel,
  .van-picker__confirm {
    color: #c41d24;
  }
}
</style>


